import * as React from 'react'
import { graphql, Link } from 'gatsby'
import Layout from '../components/layout'
import GitHub from '../components/github'
import WakaTime from '../components/wakatime'

const github = require('../utils/github/github.json')

const Delta = ({ node }) => {
  const delta = node?.fields?.delta
  if (!delta) {
    return null
  }
  const className = delta > 0 ? 'delta green' : delta < 0 ? 'delta red' : 'delta'

  return (
    <span className={className}>
      {delta}
      {delta < 0 && <span>&#9660;</span>}
      {delta > 0 && <span>&#9650;</span>}
    </span>
  )
}

const IndexPage = (props) => {
  const currentYear = new Date().getFullYear()
  const data = new Map()
  const years = new Map()
  for (const node of props.data.notes.nodes) {
    const year = node.fields.year
    const month = node.fields.month
    const path = node.fields.path
    if (!data.has(year)) {
      data.set(year, new Map())
    }
    if (!data.get(year).has(month)) {
      data.get(year).set(month, new Map())
    }
    if (!data.get(year).get(month).has(path)) {
      data.get(year).get(month).set(path, node)
    }
    years.set(year, (years.get(year) || 0) + 1)
  }

  return (
    <Layout home={true}>
      <div className="columns">
        <div>
          <h1>Notes</h1>
          <ul style={{ listStyle: 'none', paddingLeft: 0 }}>
            {Array.from(data.entries())
              .sort(([a], [b]) => b - a)
              .map(([year, months]) => (
                <li key={year}>
                  <details open={year === currentYear || year === currentYear - 1}>
                    <summary>
                      {year} <span style={{ opacity: 0.5 }}>({years.get(year)})</span>
                    </summary>
                    <ul style={{ listStyle: 'none', paddingLeft: '1em' }}>
                      {Array.from(months.entries())
                        .sort(([a], [b]) => b - a)
                        .map(([month, notes]) => (
                          <li key={month}>
                            <details open={true}>
                              <summary>
                                {month.toString().padStart(2, '0')} <span style={{ opacity: 0.5 }}>({notes.size})</span>
                              </summary>
                              <ul>
                                {Array.from(notes.entries())
                                  .sort(([a], [b]) => a > b)
                                  .map(([path, node]) => (
                                    <li key={node?.fields?.path}>
                                      <Link to={node?.fields?.path}>
                                        {node.meta?.title ||
                                          node.headings?.map((heading) => heading?.value).shift() ||
                                          path.replace(/[-_]+/g, ' ')}
                                      </Link>
                                      <Delta node={node} />
                                    </li>
                                  ))}
                              </ul>
                            </details>
                          </li>
                        ))}
                    </ul>
                  </details>
                </li>
              ))}
          </ul>
        </div>
        <div>
          <h1>Popular</h1>
          <ul>
            {props?.data?.popular?.nodes
              ?.sort((a, b) => b?.fields?.ctr - a?.fields?.ctr)
              ?.slice(0, 20)
              ?.map((node) => (
                <li key={node?.fields?.path}>
                  <Link to={node?.fields?.path}>
                    {node.meta?.title ||
                      node.headings?.map((heading) => heading?.value).shift() ||
                      node?.fields?.path.replace(/[-_]+/g, ' ')}
                  </Link>
                  <Delta node={node} />
                </li>
              ))}
          </ul>
          <h1>Contributions</h1>
          <ul>
            {github?.contributions?.edges?.map((edge) => (
              <li key={edge?.node?.url}>
                <a href={edge?.node?.url}>{edge?.node?.title}</a> - {edge?.node?.repository?.nameWithOwner}
              </li>
            ))}
          </ul>
          <h1>Stats</h1>
          <GitHub />
          <WakaTime />
        </div>
      </div>
    </Layout>
  )
}

export const Head = () => <title>Notes</title>

export const query = graphql`
  {
    notes: allMarkdownRemark(filter: { fields: { path: { ne: null }, year: { ne: null }, month: { ne: null } } }) {
      nodes {
        fields {
          path
          year
          month
          delta
        }
        meta: frontmatter {
          title
        }
        headings(depth: h1) {
          value
        }
      }
    }

    popular: allMarkdownRemark(filter: { fields: { ctr: { ne: null } } }) {
      nodes {
        id
        fields {
          path
          ctr
          delta
        }
        meta: frontmatter {
          title
        }
        headings(depth: h1) {
          value
        }
      }
    }
  }
`

export default IndexPage
