import * as React from 'react'
import Icon from './icon'
import './pie.css'

const wakatime = require('../utils/wakatime/wakatime.json')

const WakaTime = () => (
  <div>
    <p>
      and here are languages I&apos;m used to code with for last month according to <a href="https://wakatime.com/@mac">wakatime report</a>
    </p>
    <div className="pie">
      {wakatime.languages
        .filter(({ percent }) => percent > 2)
        .slice(0, 5)
        .map(({ name, percent }) => (
          <figure key={name} title={name}>
            <Icon name={name} width={32} height={32} />
            <figcaption>{percent}%</figcaption>
          </figure>
        ))}
    </div>
    <p>I&apos;m coding in</p>
    <div className="pie">
      {wakatime.editors
        // .filter(({ percent }) => percent > 2)
        // .slice(0, 10)
        .map(({ name, percent }) => (
          <figure key={name} title={name}>
            <Icon name={name} width={32} height={32} />
            <figcaption>{percent}%</figcaption>
          </figure>
        ))}
    </div>
    <p>I&apos;m coding on</p>
    <div className="pie">
      {wakatime.operating_systems
        // .filter(({ percent }) => percent > 2)
        // .slice(0, 10)
        .map(({ name, percent }) => (
          <figure key={name} title={name}>
            <Icon name={name} width={32} height={32} />
            <figcaption>{percent}%</figcaption>
          </figure>
        ))}
    </div>
  </div>
)

export default WakaTime
