import * as React from 'react'
import Icon from './icon'
import pullrequest from '../images/icons/github/pullrequest.svg'
import commit from '../images/icons/github/commit.svg'
import issue from '../images/icons/github/issue.svg'
import star from '../images/icons/github/star.svg'
import merge from '../images/icons/github/merge.svg'
import './pie.css'

const github = require('../utils/github/github.json')

const arr = github.languages.repositories.nodes
  .map((node) => node.languages.edges.map((edge) => edge.node.name))
  .reduce((acc, x) => acc.concat(x), [])

const languages = Object.entries(arr.reduce((acc, x) => Object.assign(acc, { [x]: (acc[x] || 0) + 1 }), {}))
  .sort((a, b) => b[1] - a[1])
  .map((entry) => [entry[0], parseFloat(((entry[1] / arr.length) * 100).toFixed(2))])
  .filter((entry) => entry[1] > 2)
  .splice(0, 5)
  .map((entry) => ({ language: entry[0], percentage: entry[1] }))

const inlineIconStyle = {
  marginLeft: '0.75em',
  marginRight: '0.25em',
  marginBottom: '-1px'
}

const GitHub = () => (
  <div>
    <p>
      Some github stats:
      <span title="pullrequests">
        <img src={pullrequest} alt="pullrequests" width="14" height="14" style={inlineIconStyle} />
        {github?.stats?.pullRequests?.totalCount}
      </span>
      <span title="commits">
        <img src={commit} alt="commits" width="14" height="14" style={inlineIconStyle} />
        {github?.stats?.contributionsCollection?.totalCommitContributions}
      </span>
      <span title="issues">
        <img src={issue} alt="issues" width="14" height="14" style={inlineIconStyle} />
        {github?.stats?.issues?.totalCount}
      </span>
      <span title="stars">
        <img src={star} alt="stars" width="14" height="14" style={inlineIconStyle} />
        {github?.stats?.repositories?.nodes?.map((node) => node?.stargazers?.totalCount).reduce((a, b) => a + b)}
      </span>
      <span title="contributions">
        <img src={merge} alt="contributions" width="14" height="14" style={inlineIconStyle} />
        {github?.stats?.repositoriesContributedTo?.totalCount}
      </span>
    </p>
    <p>According to github stats here are languages used in repositories under my account</p>
    <div className="pie">
      {languages.map(({ language, percentage }) => (
        <figure key={language} title={language}>
          <Icon name={language} width={32} height={32} />
          <figcaption>{percentage}%</figcaption>
        </figure>
      ))}
    </div>
  </div>
)

export default GitHub
